.rmdb-navigation {
	display: flex;
	align-items: center;
	width: 100%;
	height: 40px;
	background: #353535;
}

.rmdb-navigation-content p,
.rmdb-navigation-content a {
	color: #fff;
	text-decoration: none;
	margin: 0px;
}

.rmdb-navigation-content {
	max-width: 1280px;
	margin: 0 auto;
	padding: 0 20px;
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.rmdb-navigation-content p {
	font-family: 'Abel', sans-serif;
	font-size: 22px;
	padding-right: 10px;
	text-decoration: none;
}

@media screen and (max-width: 768px) {
	.rmdb-navigation-content p {
		font-size: 16px;
	}
}
